import { all, fork, takeEvery, takeLatest, call, put, take, delay } from 'redux-saga/effects';
import { LOG_IN_SUCCESS, LOG_IN_FAILURE, LOG_IN } from '../reducers/user';

// all  -> redux-saga에서 eventListener와 비슷하게 while(true)를통해  실행대기 함수등록을 하였다면, 모두 묶어줄 때 사용한다.
// take -> redux-saga에서 generator.next()와 마찬가지로 yield 로인해 중단된 함수를 다시 재개시킨다.
// put  -> redux-saga에서의 dispatch라 생각하면 된다.
// takeEvery -> redux-saga에서 while(true)와같이 지속 리스닝을 위한 지원 effect
// takeLatest -> takeEvery와 겉보기에는 별차이 없어보인다. 하지만 동시에 여러번의 작업이 들어왔을때 마지막 부분만 실행한다
// 예를 들자면 ex) 로그인 버튼을 광클릭 하게 되었을때 여러번 요청 가는것을 방지하기위해 마지막것만 처리해준다. 
// takeEvery, takeLatest의 사용 구분은, 동시에 여러작업이 들어왔을대 모든것들을 처리해줄 것이냐, 마지막것만 처리해줄 것이냐로 구분하면 된다.
const HELLO_SAGA = 'HELLO_SAGA';

function* loginAPI() {
  // 서버에 요청을 보내는 부분
}

function* login() {
  try {
    yield call(loginAPI);
    yield put({ // loginAPI 성공 put은 dispatch와 동일
      type: LOG_IN_SUCCESS
    });
  } catch (e) { // loginAPI 실패
    console.error(e);
    yield put({
      type: LOG_IN_FAILURE
    });
  }
}

function* watchLogin() {
  yield takeEvery(LOG_IN, login);
  yield delay(2000);
  yield put({
    type: LOG_IN_SUCCESS,
  })
}
function* hello() {
  yield delay(1000);
  yield put({
    type: 'BYE_SAGA'
  })
}
function* watchHello() {
  yield takeLatest(HELLO_SAGA, hello);
}

function* watchSignUp() {
}


export default function* userSaga() {
  yield all([
    fork(watchHello()),
    fork(watchLogin()),
    // watchSignUp(),
  ]);
}